export default {
    data() {
        return {
            isVisible: false
        };
    },
    methods: {
        scrollToTop() {
            window.scroll({
                top: 0,
                behavior: "smooth",
            });
        },
    },

    mounted() {
        window.addEventListener("scroll", () => {
        let scroll = window.scrollY;
            if (scroll >= 500) {
                this.isVisible = true;
            } else {
                this.isVisible = false;
            }
        });
    },
};
